@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

::before, ::after{
  box-sizing: border-box;
}

:root{
  --grey: #F2F2F2;
  --grey1: #DADADA;
  --grey2: #bdbdbd;
  --grey3: #595959;
  --text-color: #454545;
  --text-color-rgb: rgb(69,69,69);
  --shadow: 2px 2px 2px 1px rgba(0,0,0,0.05);
  --shadow-card: 0px 0px 20px rgba(0,0,0,0.1);
  --white: #FFFFFF;
  --green: #DDEFEC;
  --green1: #B2E0D8;
  --green2: #8BBEB5;
  --green3: #5FA397;
}

html, body, a, input, button, textarea{
  font-family: 'Poppins', sans-serif;
  text-decoration: none;
  outline: none;
  color: #454545;
  color: var(--text-color);
  scroll-behavior: smooth;
}

body{
  background: #f7f7f7;
}

.App {
  padding: 20px;;
}


/*-------------------- landing page --------------------*/
/*-------------------- navbar landing page desktop --------------------*/

nav.navbar { 
  position: fixed;
  top:0;
  display: flex;
  justify-content: space-between;
  padding: 20px 20px 10px 20px;
  width: 100%;
  background: #FFFFFF;
  background: var(--white); 
  box-shadow: 2px 0 10px #DADADA; 
  box-shadow: 2px 0 10px var(--grey1);
}

nav.navbar .navbar-user{
  margin-left: 10px;
}

button.navbar-button, .join-signup {
  padding: 2px 10px;
  font-size: 16px;
  font-weight: 500;
  width: 100px;
  border: none;
  outline: none;
  border-radius: 5px;
  color: #454545;
  color: var(--text-color);
  cursor: pointer;
}

button.login-btn {
  background: #DDEFEC;
  background: var(--green);
  border: 2px solid #DDEFEC;
  border: 2px solid var(--green);
}

button.signup-btn {
  background: #B2E0D8;
  background: var(--green1);
  border: 2px solid #B2E0D8;
  border: 2px solid var(--green1);
}

#home-btn {
  text-decoration: none;
  display: inline-flex;
}

#home-btn h4{
  font-size: 20px;
  font-weight: 600;
  color: #454545;
  color: var(--text-color);
}

#home-btn span img{
  width: 30px;
  height: 30px;
  margin-right: 10px;
}

.auth-section{
  display: inline-flex;
}

/*-------------------- navbar home desktop --------------------*/
.profile-navbar{
  display: flex;
  flex-direction: row;
  align-items: center;
}

.logout-icon{
  font-size: 20px;
}

/*-------------------- login / signup --------------------*/
.container-page{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.login, .signup{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 100px auto 0 auto;
}

.login h1, .signup h1{
  font-weight: 600;
  margin-bottom: 20px;
}

.form-auth{
  display: flex;
  flex-direction: column;
  margin: 0 auto 40px auto;
}

.container-page .input-field {
  position: relative;
  width: 250px;
  height: 44px;
  line-height: 44px;
  margin: 20px 0;
}

.container-page label {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  color: #d3d3d3;
  transition: 0.2s all;
  cursor: text;
}

.container-page input {
  width: 100%;
  border: 0;
  outline: 0;
  padding: 0.5rem 0;
  border-bottom: 2px solid #d3d3d3;
  box-shadow: none;
  color: #111;
}

.container-page input:invalid {
  outline: 0;
}

.container-page input:focus,.container-page input:valid {
  border-color: #5FA397;
  border-color: var(--green3);
}

.container-page input:focus ~ label,.container-page input:valid ~ label {
  font-size: 14px;
  top: -24px;
  color: #5FA397;
  color: var(--green3);
}

.container-page .submit-input{
  border: none;
  width: 150px;
  margin: 0 auto;
  margin-top: 20px;
  border-radius: 5px;
  background-color: #B2E0D8;
  background-color: var(--green1);
  color: black;
  font-size: 16px;
  font-weight: 600;
}

.signup p, .login p{
padding: 30px 0 10px 0;
font-size: 14px;
color: #bdbdbd;
color: var(--grey2);
}

.signup a, .login a{
  color: #5FA397;
  color: var(--green3);
  font-weight: 500;
}





/*-------------------- landing page container --------------------*/
.header{
