  font-size: 16px;
  margin-bottom: -5px;
}
.header-text-container h1{
  font-weight: 600;
  font-size: 30px;
  letter-spacing: 1px;
}
.header-text-container h2{
  font-weight: 300;
  font-size: 20px;
  line-height: 25px;
}
.join-signup{
  background-color: black;
  text-align: center;
  width: 150px;
  padding: 10px 15px;
  margin-top: 20px;
}
.join-signup-btn{
  background-color: transparent;
  color: #FFFFFF;
  color: var(--white);
  border: none;
  outline: none;
  font-size: 20px;
