  height: 30px;
  object-fit: cover;
}


/*-------------------- profile --------------------*/
.container-profile{
  padding: 20px 15px 5px 15px;
}

.bg{
  height: 160px;
  width: 100%;
  overflow: hidden;
  position: relative;
  z-index: -9999;
  }

.bg-color{
  background-color: #DDEFEC;
  background-color: var(--green);
}
 
.bg img{
  width: 100%;
  height: 160px;
  object-fit: cover;
  display: block;
  position: relative;
  z-index: -1;
}

.logout{
  position: absolute;
  width: 45px;
  height: 45px;
  margin: 20px 20px 0 0;
  border-radius: 50%;
