  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 25px;
  margin-top: -25px;
}

.user-info .a-btn {
  border-radius: 5px;
  outline: none;
  width: 80px;
  text-align: center;
  border:none;
  background-color: #8BBEB5;
  background-color: var(--green2);
  color: #FFFFFF;
  color: var(--white);
  padding: 3px 10px;
  font-size: 16px;
}

.user-info-container{
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-left: 10px;
}

.edit-user-info{
  margin-top: -6px;
}

.user-info-container h4 {
  font-size: 16px;
  color:#595959;
  color:var(--grey3);
  font-weight: 500;
}

.profile-picture2, .profile-picture2 img{
  height: 100px;
  min-width: 100px;
  max-width: 100px;
  border-radius:50%;
  margin-top: -30px;
  object-fit: cover;
}


.profile-picture3, .profile-picture3 img{
  height: 35px;
  width: 35px;
  border-radius:50%;
  object-fit: cover;
}

.profile-stroke{
  border:5px solid white;
}

.profile-stroke1{
  border:2px solid white;
}

/*----------- navbar profile (meals, plans, save) -------------------*/

.nav-profile{
  position: relative;
}

.nav-profile>hr{
  margin-top: 65px;
