  border-bottom: 1px solid var(--grey2);
}

/*img upload*/
.upload-img-text [type="file"]{
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  white-space: nowrap;
  width: 1px;
}

.img-text-box{
  margin-top: 15px;
  background-color: #F2F2F2;
  background-color: var(--grey);
  border-radius: 5px;
  height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.img-text-box p{
  width: 130px;
  text-align: left;
  font-size: 19px;
  font-weight: 300;
  line-height: 28px;
}

.img-text-box ion-icon{
  font-size: 30px;
  margin-right: 10px;
  color: #454545;
  color: var(--text-color);
}

.upload-img-text [type="file"] + .img-text-box {
  cursor: pointer;
}

/*portions section*/
.portions-section{
