/*-------------------- recipes details -------------------*/
.image-recipe{
  height: 500px;
  width: 100%;
  overflow: hidden;
  position: relative;
}

.image-recipe span{
  position: absolute;
  padding-top: 5px;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 60px 0 0 30px;
  background-color: rgba( 0, 0, 0, 0.6);
}

.image-recipe span ion-icon{
  color: #FFFFFF;
  color: var(--white);
  font-size: 26px;
}

.image-recipe>img{
  /* width: 100%; */
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}
.recipe-wrapped{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  /* margin-bottom: 130px; */
}
.container-recipe-details{
  /* position: absolute; */
  margin-top: -130px;
  width: 300px;
  min-height: 100px;
  padding: 15px 30px;
  background: #DDEFEC;
  background: var(--green);
  border-radius: 10px;
  margin-bottom: 30px;
  box-shadow: 0px 0px 20px rgba(0,0,0,0.1);
  box-shadow: var(--shadow-card);
}
.container-recipe-details h1{
  font-size: 25px;
  margin-bottom: 15px;
  font-weight: 500;
}

.container-recipe-details .media{
display: flex;
justify-content: space-between;
}

.container-recipe-details .media>div{
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 14px;
  margin: 5px 0 20px 0;
}

.container-recipe-details .media>div ion-icon{
  font-size: 20px;
}

.author{
  display: flex;
  align-items: center;
}

.author h4{
  font-size: 16px;
  font-weight: 300;
  margin-left: 10px;
}

.info-details-recipe{
  display: flex;
  justify-content: space-between;
  margin: 20px;
}

.info-details-recipe article{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 10px;
}

.info-details-recipe span{
  border: 2px solid #8BBEB5;
  border: 2px solid var(--green2);
  width: 80px;
  height: 80px;
  border-radius: 50%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-align: center;
  margin-bottom: 10px;
  font-weight: 400;
  font-size: 16px;
}

.info-details-recipe p {
  font-size: 14px;
  font-weight: 500;
}

.info-recipe-ingred-steps{
  padding: 40px 20px 60px 20px;
}

.info-recipe-ingred-steps article{
  margin-bottom: 40px;
}

.info-recipe-ingred-steps article h3{
  font-size: 18px;
  letter-spacing: 0.5px;
  font-weight: 600;
  margin-bottom: 20px;
}

.info-recipe-ingred-steps ul{
  list-style: none;
  padding: 0 20px;
  /* word-break: keep-all; */
  -webkit-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto;
}

.info-recipe-ingred-steps ul .list-ingredients{
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 10px;
}

.info-recipe-ingred-steps ul li input[type="checkbox"] {
  position: relative;
  width: 20px;
  height: 20px;
  color: #363839;
  border: 1px solid #bdc1c6;
  border-radius: 5px;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  outline: 0;
  cursor: pointer;
  margin-right: 10px;
  transition: background 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
}

.info-recipe-ingred-steps ul li input[type="checkbox"]::before {
  position: absolute;
  content: '';
  display: block;
  top: 2px;
  left: 6px;
  width: 6px;
  height: 12px;
  border: 1px solid #FFFFFF;
  border: 1px solid var(--white);
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
  opacity: 0;
}

.info-recipe-ingred-steps ul li input[type="checkbox"]:checked {
  color: #FFFFFF;
  color: var(--white);
  border-color: #8BBEB5;
  border-color: var(--green2);
  background: #8BBEB5;
  background: var(--green2);
}
.info-recipe-ingred-steps ul li input[type="checkbox"]:checked::before {
  opacity: 1;
}

.info-recipe-ingred-steps ul li input[type="checkbox"]:checked ~ label::before {
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
          clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
}

