/*-------------------- scroll options --------------------*/

.scrollable {
  width: 500px;
  overflow-x: scroll;   
  /* display: none; */
  display:flex;
  flex-direction: row;
  width: 100%;
  padding-left: 10px;
}
.scrollable::-webkit-scrollbar {
  display: none;
} 


/*-------------------- categories slider --------------------*/

.category-card{
  width: 140px;
  height: 100px;
  position: relative;
  border-radius: 5px;
  margin: 0 10px;
}

.category-card h3{
  position: absolute;
  bottom:5px;
  color:white;
  z-index: 1;
  margin-left:15px;
  margin-bottom:5px;
  font-weight: 500;
  letter-spacing: 1px;
}

.category-card img{
  width: 140px;
  height: 100px;
  border-radius: 5px;
  /* width: 100%; */
  object-fit: cover;

}

.category-card div{
  width: 140px;
  height: 100px;
  border-radius: 5px;
  position: absolute;
  background-color: rgba(0,0,0,0.6);
}


/*-------------------- card recipe (home, search) --------------------*/
.card-recipe{
  border-radius:10px;
  margin: 0 20px 50px 20px;
  background: #FFFFFF;
  background: var(--white);
  box-shadow: 0 0px 10px #DADADA;
  box-shadow: 0 0px 10px var(--grey1);
}

.card-recipe img{
  border-radius:10px 10px 0px 0px;
  background-color: rgb(206, 233, 213);
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.card-recipe h3{
  font-size: 16px;
  font-weight: 500;
  color:#454545;
  margin:0 0 20px 0;

}
.card-recipe span{
  font-size: 16px;
  color: #595959;
  color: var(--grey3);
  margin-left: 5px;

}
.icons-container>div{
  display:flex;
  align-items: center;
}

.info-card-recipe{
  border-radius:0px 0px 10px 10px;
  padding:5px 20px 20px 20px;
}

.icons-container{
  display:flex;
  justify-content: space-between;
  align-items: center;
}

.icons-container ion-icon{
  font-size:25px;
  color: #595959;
  color: var(--grey3);
}


/*--------------------- search bar -------------------------*/
.searchbar{
  display: flex;
  align-items: center;
  margin: -5px 25px 20px 25px;
}

.searchbar input{
  background-color: none;
}

.searchbar input{
  position: relative;
}

.searchbar ion-icon{
  position: absolute;
  z-index: 1;
  right: 45px;
  font-size: 20px;
  color: #5FA397;
  color: var(--green3);
}

.input-search-bar{
  width: 100%;
  margin: 0 auto;
  padding: 10px 30px;
  border: 1px solid #e6e6e6;
  color: #454545;
  color: var(--text-color);
  font-size: 16px;
  border-radius: 25px;
  background: #ffffff;
  box-shadow: inset 4px 6px 10px #ebebeb, 
              inset -3px -3px 3px #ffffff;
}

.input-search-bar::-webkit-input-placeholder{
  color: #bdbdbd;
  color: var(--grey2);
}

.input-search-bar:-ms-input-placeholder{
  color: #bdbdbd;
  color: var(--grey2);
}

.input-search-bar::-ms-input-placeholder{
  color: #bdbdbd;
  color: var(--grey2);
}

.input-search-bar::placeholder{
  color: #bdbdbd;
  color: var(--grey2);
}


